.aboutMainComp {
	margin-top: 80px;
	min-height: 300px;
	max-height: 100%;
	background: rgba(221, 232, 240, 0.801);
    padding: 20px;
    margin-bottom: 60px;
}

.row1 {
	background: rgb(196, 212, 221);
	margin-bottom: 40px;
	padding: 20px;
	border: 2px solid white;
}

.par1 {
	font-size: 22px;
	text-align: left;
	padding-top: 40px;
}

.aboutTitle {
	color: rgb(3, 0, 0);
	text-shadow: -1px 0 rgb(252, 249, 249), 0 3px rgb(252, 250, 250),
		3px 0 rgb(252, 251, 251), 0 -1px rgb(248, 247, 247);
}

.aboutTitles {
	color: rgb(3, 0, 0);
	text-shadow: -1px 0 rgb(252, 249, 249), 0 3px rgb(252, 250, 250),
		3px 0 rgb(252, 251, 251), 0 -1px rgb(248, 247, 247);
}

video {
	width: 700px;
}

.arrowDown {
	width: 60px;
}

@media (min-width: 350px) and (max-width:410px) {
	.aboutTitle {
		font-size: 26px;
	}

	.aboutTitles {
		font-size: 26px;
	}

	.arrowDown {
		width: 40px;
	}

	.aboutMainComp {
		margin-top: 10px;
	}

	.par1 {
		font-size: 18px;
		max-height: 400px;
		text-align: justify;
	}

	.videoInstructionsReg {
		display: none;
	}
}

@media (max-width: 768px) {
    .aboutMainComp {
        margin-top: 160px;
        margin-bottom: 80px;
    }

    video {
		width: 550px;
	}
}
