.profileMainContainer {
	color: white;
	margin-top: 80px;
}

.mainBox {
	margin: 0 0 20px -40px;
}

.guestListContent {
	color: black;
}

.eventDetailsText {
	font-size: 26px;
	color: rgb(204, 230, 255);
	text-align: center;
	margin-bottom: 10px;
}

.datesText {
	text-align: center;
	font-size: 17px;
}

.guestListBox {
	padding: 20px;
	background: rgb(209, 235, 209);
}

.guestListButtons {
	margin: 5px;
	border: 1px solid white;
}

.confirmedNoBox {
	padding: 20px;
	background: rgb(243, 245, 243);
}

.deleteButton {
	background: rgb(173, 14, 14);
}

.editButton {
	background: rgb(72, 143, 139);
}

.confirmed {
	background: green;
}

.guestListTitleModal {
	text-align: center;
}

.usernameLoggedin {
	position: absolute;
	margin-left: 72%;
	margin-top: -130px;
}

.card-body {
	background: rgb(43, 43, 44);
}

.profilePageMainTitle {
	background: rgba(196, 210, 212, 0.699);
	padding: 20px;
	text-shadow: -1px 0 black, 0 3px black, 3px 0 black, 0 -1px black;
}

.logoutBtn {
	background: rgb(19, 21, 48);
	padding: 5px;
	margin-top: -330px;
	margin-left: -10px;
	justify-self: center;
	align-self: center;
	width: 120px;
	grid-row: 1;
	border-radius: 10px;
	font-size: 18px;
	color: white;
	border: solid 1px white;
	margin-bottom: 15px;
	text-shadow: -1px 0 black, 0 2px black, 2px 0 black, 0 -1px black;
}

.modalToEdit {
	background: rgba(178, 188, 192, 0.171);
}

.deleteBtnEvent {
	background: rgb(173, 17, 17);
	font-size: 16px;
	padding: 10px;
	color: white;
	border-radius: 5px;
}

.editEventList {
	background: rgb(48, 179, 113);
	margin-top: 15px;
	font-size: 16px;
	padding: 10px;
	color: white;
	border-radius: 5px;
}

.addGuestsEventList {
	background: rgb(24, 41, 5);
	margin-top: 15px;
	font-size: 16px;
	padding: 10px;
	color: white;
	border-radius: 5px;
}

.viewGuestsEventList {
	background: rgb(13, 24, 20);
	margin-top: 15px;
	font-size: 16px;
	padding: 10px;
	color: white;
	border-radius: 5px;
}

.modalMsg {
	position: absolute;
	margin-top: -25px;
	margin-left: 83%;
	font-size: 18px;
	padding: 10px;
	border-radius: 10px;
	color: rgb(255, 250, 250);
	background: rgb(33, 33, 34);
}

.profileContainer {
	margin-top: 20px;
	text-align: center;
}

.calendarBox {
	margin-top: 20px;
}

.noEventYetMessage {
	text-align: center;
	font-size: 20px;
}

.listOfGuestsToInvite {
	background: rgba(123, 144, 172, 0.733);
	padding: 20px;
	border: 2px solid rgb(241, 240, 240);
	text-align: left;
	justify-self: center;
	max-height: 600px;
	overflow: scroll;
	text-shadow: -1px 0 black, 0 3px black, 3px 0 black, 0 -1px black;
}

@media (min-width: 415px) and (max-width: 768px) {
	.homeTitleWelcome {
		margin-top: 10px;
	}

	.homeTextInParagraph {
		margin-top: 40px;
	}

	.profileMainContainer {
		margin-top: 120px;
	}

	.usernameLoggedin {
		position: relative;
		left: 20;
		margin-top: -130px;
	}
}

@media (max-width: 360px) {
	.profilePageMainTitle {
		font-size: 26px;
		margin-top: 10px;
	}

	.guestListModalMobile {
		max-height: calc(100vh - 80px);
		overflow-y: auto;
	}

	.modalForMobile {
		/* position: fixed; */
		width: 280px;
		transform: translate(-50%, -50%);
		max-height: calc(100vh - 80px);
		overflow-y: auto;
	}

	#buttonToSeeCalendarMobile {
		visibility: visible;
	}

	.profileMainContainer {
		margin-bottom: 0px;
	}

	.profileTitleSection {
		margin-top: -40px;
	}
}
